:root {
    --review-width: 400px;
    --duration: 250ms;
}

.Review {
    min-width: var(--review-width);
    width: 80%;
    padding: 1em;
    margin: 0 auto;
    box-shadow: 0 0 1em black;
    border-radius: 5px;
    border: 2px solid #333;
}

.Review__title {
    font-weight: 600;
    font-size: 1.6rem;
    padding-bottom: 0.2em;
    margin-bottom: 1rem;
    border-bottom: 1px solid #333;
}

.Review__post {
    border-left: 2px solid greenyellow;
    padding-left: 1em;
}

.Review__title--owner, .Review__title--name {
    color: deepskyblue;
    margin: 0 0.3em;
}

.Review__current, .Review__buttons {
    display: flex;
    max-width: 600px;
    width: 90%;
    margin: 1em auto;
}

.Review__current {
    /* color: darkslategrey; */
    min-width: var(--review-width);
    padding: 3rem;
    box-shadow: 0 0 1em black;
    justify-content: center;
    align-items: center;
    border-radius: 5px;
    perspective: 700px;
    font-weight: 200;
}

.ReviewCard {
    min-width: var(--review-width);
    height: 100%;
    background: linear-gradient(-30deg, #eee, #ddd);
    color: darkslategrey;
    font-size: 1.6rem;    
    line-height: 1.8rem;
    display: flex;
    place-self: center;
    
}

.fadein {
    animation: var(--duration) ease 1 fadein;
    animation-fill-mode: forwards;
}

/* @todo rotation needs to be on a shadow element, because I don't want text content to be rotated (text becomes blurry, haven't found a fix) */
/* note also that quickly hovering and unhovering causes the whole perspective to bug out */
.flip { 
    transform: perspective(500px);
    transform-style: preserve-3d;
    transform-origin: 50% 50%; 

    animation: 250ms linear flip;
    animation-fill-mode: forwards;

}

@keyframes fadein {
    0% {
        opacity: 0;
        
    }
    100% {
        opacity: 1;
    }
}

@keyframes flip {
    0% {
        opacity: 1
    }
    50% {
        transform: rotateX(90deg)
    }
    100% {
        transform: rotateX(0deg);
    }
}

.Review__buttons {
    min-width: var(--review-width);
    justify-content: space-around;
}

.Review-button {
    outline: none;
    margin: none;
    border: none;
    color: azure;
    box-shadow: 0 0 0.5em black;
    margin: 0 1em;
    width: 120px;
    height: 30px;
    /* font-weight: 600; */
    font-family: 'Roboto';

    transition: all 80ms ease-in
}

.Review-button:hover {
    padding: 0;   /* padding is non-zero by default which causes 'shifting' effect of text */
    transform: translateY(-1px) scale(1.03);
    transform-style: preserve-3d;
    transform: center;
    box-shadow: 0 5px 1em -6px #333;

}

.fail, .pass {
    background-color: var(--background-secondary);
}

.fail:hover, .fail:focus {
    background-color: orangered;
}

.pass:hover, .pass:focus {
    background-color: seagreen;
}

.Review-progress__wrapper {
    width: 100vw;
    position: absolute;
    top: 3rem;
    left: 0;
    height: 4px;
    background-color: #555;
}

#Review-progress__bar {
    background-color: deepskyblue;   
    height: inherit;     
    transition: width 300ms linear;
}